.wrapper {
  height: 100%;
  overflow-y: auto;
}
.wrapper_info {
  display: flex;
  align-items: center;
}
.wrapper_info > li:first-child {
  flex: 1;
  color: #000;
}
@media screen and (max-width: 321px) {
  .wrapper_info > li:first-child {
    /*小*/
    font-size: 16px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info > li:first-child {
    /*中*/
    font-size: 17px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info > li:first-child {
    /*大*/
    font-size: 19px;
  }
}
.wrapper_info > li:first-child > div {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.wrapper_info > li:first-child > div img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
@media screen and (max-width: 321px) {
  .wrapper_info > li:last-child {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_info > li:last-child {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_info > li:last-child {
    /*大*/
    font-size: 17px;
  }
}
.wrapper_info > li:last-child > i:first-child {
  color: #2FCE31;
  margin-right: 2px;
}
.wrapper_info > li:last-child > i:last-child {
  color: #FF0000;
  margin-left: 10px;
}
.wrapper_info > li {
  display: flex;
  align-items: center;
}
.wrapper_extra :global(.am-list-content) {
  display: flex;
}
.wrapper_extra :global(.am-list-content) > div {
  color: #AEAEAE;
}
@media screen and (max-width: 321px) {
  .wrapper_extra :global(.am-list-content) > div {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_extra :global(.am-list-content) > div {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_extra :global(.am-list-content) > div {
    /*大*/
    font-size: 15px;
  }
}
